.h-wrapper{
    background: rgb(220, 220, 220);
    color: black;
    
}
.header{
font-family: 'Nirmala UI';
font-style: normal;
font-weight: 700;
font-size: 36px;
line-height: 48px;
}
.h-container{
    justify-content: space-between;
    padding-top: 1rem;
    padding-bottom: 1rem;
    font-weight: bolder;
    color: #000;
}
.header{
    font-weight: 900;
}
.h-menu{
    gap: 2rem;
}
.h-menu > *:hover{
    background-color: #adadad;
    border-radius: 10px;
    cursor: pointer;
}
