body{
    font: 16px / 32px 'impact', 'Arial', 'Serif';
    background-color: silver;
}
.header h1{
    font-size: 80px;
    margin: 40px;
    text-align: center;
}
.container{
    width: 600px;
    height: 120px;
    margin: auto;
    padding: 10px;
    background-color: black;
    border-radius: 10px;
}
.image .li{
    width: 158px;
    height: 163px;
    margin-left: 20px;
    padding-right: 215px;
    position: relative;
    border-radius: 10px;
    float: left;
}
.image img{
    border-radius: 10px;
}
.image a{
    color: white;
}
.image .li span{
    position: absolute;
    display: block;
    width: 150px;
    height: 163px;
    padding: 20px 10px 20px 10px;
    background-color: rgba(255, 255, 255, .5);
    top: 0;
    line-height: 15px;
    text-align: justify;
    opacity: 0;
    transition: 0.8s;
    color: black;
    border-radius: 10px;
    
}
.image .li a:hover span{
    opacity: 1;
}
.img1{
    width: 150px;
    height:163px;
}
